.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
}

.placeholder-bkg {
  border-bottom: 1px solid black;
  background-color: #121212;
  color: #ecf0f1;
}

.placeholder-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.placeholder-font-weight {
  font-weight: bold;
}

.placeholder-color {
  color: #3498db;
}

.placeholder-underline {
  border-bottom: 4px solid #ecf0f1;
}

.introduction-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 12px;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.introduction-border-anims {
  position: absolute;
  opacity: 0;
  left: 0px;
  width: 0%;
  height: 0%;
  box-sizing: border-box;
  border: 4px solid #ecf0f1;
  -moz-transition: width 750ms;
  -webkit-transition: width 750ms;
  -o-transition: width 750ms;
  transition: width 750ms;
  -moz-animation: expand 750ms ease-in-out;
  -webkit-animation: expand 750ms ease-in-out;
  -o-animation: expand 750ms ease-in-out;
  animation: expand 750ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -moz-animation-delay: 500ms;
  -webkit-animation-delay: 500ms;
  -o-animation-delay: 500ms;
  animation-delay: 500ms;
}

.introduction-text {
  color: #ecf0f1;
  opacity: 0;
  animation: appear 250ms ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes expand {
  0% {
    opacity: 0;
    width: 0%;
    height: 0%;
  }
  10% {
    opacity: 1;
    width: 0%;
    height: 0%;
  }
  30% {
    opacity: 1;
    width: 0%;
    height: 100%;
  }
  100% {
    opacity: 1;
    width: 100%;
    height: 100%;
  }
}

/* Typography */
.h1 {
  font-size: 6em;
  font-weight: lighter;
  letter-spacing: -1.5px;
}

.h2 {
  font-size: 3.75em;
  font-weight: lighter;
  letter-spacing: -0.5px;
}

.h3 {
  font-size: 3em;
  letter-spacing: 0px;
}

.h4 {
  font-size: 2.125em;
  letter-spacing: 0.25px;
}

.h5 {
  font-size: 1.5em;
  letter-spacing: 0px;
}

.h6 {
  font-size: 1.25em;
  font-weight: bold;
  letter-spacing: 0.15px;
}

.subtitle1 {
  font-size: 1em;
  letter-spacing: 0.15px;
}

.subtitle2 {
  font-size: 0.875em;
  font-weight: bold;
  letter-spacing: 0.1px;
}

.body1 {
  font-size: 1em;
  letter-spacing: 0.5px;
}

.body2 {
  font-size: 0.875em;
  letter-spacing: 0.25px;
}

.button {
  font-size: 0.875em;
  font-weight: bold;
  letter-spacing: 1.25px;
  text-transform: uppercase;
}

.caption {
